export default {
	props: {
		shareShow: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			show: false,
			flag: true,
			$src: this.$src
		}
	},
	methods: {
		// 关闭分享
		shareClose() {
			this.show = false;
			this.$emit('shareClose',false);
			this.flag = true;
		},
		// 点击微信分享
		wxShare() {
			this.show = true;
			this.flag = false;
			// 执行微信分享功能
			this.$emit('wxShare')
		},
		// 海报
		getImg() {
			this.$emit("getImg")
		}
	}
}
